<template>
  <div class="how-it-works">
    <div>
      <VueSlickCarousel v-bind="slickOptions" ref="carousel">
        <div v-for="n in 3" v-bind:key="n">
          <div class="image-wrapper">
            <img class="image" :src="$t('howitworks.slide' + n + '~image')" alt="">
          </div>
          <div class="title" v-html="$t('howitworks.slide' + n + '~title')"></div>
          <div class="question">{{ $t("howitworks.question") }}</div>
          <div class="list">
            <div v-for="j in 3" v-bind:key="j" class="list__item">
              <span>{{ j }}</span>{{ $t('howitworks.slide' + n + '~info~' + j) }}
            </div>
          </div>
        </div>
      </VueSlickCarousel>
      <a v-if="isAuthenticated()" @click="doPlay()" class="how-it-works-cta cta">{{ $t('howitworks.cta~playNow') }}</a>
      <a v-else @click="doLogin()" class="how-it-works-cta">{{ $t('howitworks.cta~login') }}</a>

      <button type="button" class="show-terms" @click="isConditionsExpanded = !isConditionsExpanded;">
        <template v-if="isConditionsExpanded">
          {{ $t("gift.hideTermsAndConditions") }}
        </template>
        <template v-else>{{ $t("gift.showTermsAndConditions") }}</template>
      </button>
      <div v-if="isConditionsExpanded" class="terms-and-conditions">
        <ol>
          <li>Hem mini oyuna, hem de gnlk kampanyalara katlabilmek iin son 7 gn iinde min 100 deme yapm olmanz gereklidir.</li>
          <li>Kampanya gn yaplan demeler o gnn kampanyasna katlm iin geerli deildir.</li>
          <li>Sadece kampanya gn Casino Olimpiyatlar uygulamasn aan oyuncularmz, gnn kampanyasndan yararlanabiliyor olacaktr.</li>
        </ol>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import VueSlickCarousel from "vue-slick-carousel";
import "vue-slick-carousel/dist/vue-slick-carousel.css";
import globalMixin from "@/mixins/global";

export default {
  name: "HowItWorks",
  components: {
    VueSlickCarousel
  },
  mixins: [globalMixin],
  computed: {
    ...mapGetters(["isFirstTime", "isAuthenticated"]),
    ctaText() {
      if (this.isLoggedIn && this.todaysGift)
        return this.$t("guest.action.letsgo");
      else if (!this.isLoggedIn && this.todaysGift)
        return this.$t("guest.action.login");
      else if (this.isLoggedIn && !this.todaysGift)
        return this.$t("guest.action.noGiftLetsgo");
      else if (!this.isLoggedIn && !this.todaysGift)
        return this.$t("guest.action.noGiftLogin");
      return "";
    }
  },
  data: function() {
    return {
      slickOptions: {
        slidesToShow: 1,
        arrows: false,
        dots: true,
        autoplay: false,
        slidesToScroll: 1,
        infinite: false,
      },
      isConditionsExpanded: false,
    }
  },
  methods: {
    doPlay: function() {
      if (this.isFirstTime()) {
        this.$store.dispatch("clearFirstTime");
      }
      this.$router.push("/lobby").catch(()=>{});
    },
    doLogin: function() {
      this.closeApp(true);
      window.olympics2022Settings.openLoginPage();
    },
  },
};
</script>

