<template>
  <div :class="{'revealed': $route.params.date}">
    <Train :overlay="true" :date="date"></Train>
    <Gift :date="date" />
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import Gift from "@/components/Gift/Gift.vue";
import { localDate } from "@/utils/date";
import Train from "@/components/Train/Train.vue";

export default {
  name: "revealed",
  components: {
    Gift,
    Train
  },
  computed: {
    ...mapGetters(["getGiftByDate"]),
    date() {
      return this.$route.params.date || localDate;
    }
  }
};
</script>

