<template>
  <section class="previous-gifts">
    !PreviousGifts!
    <h2 class="title">{{ $t("misc.previous-gifts") }}</h2>

    <ul class="gifts-list">
      <li v-for="(date, index) in previousGiftsDates" :key="index">
        <router-link
          :to="{ name: 'revealed', params: { date } }"
          class="gifts-link"
          >{{ date | formatDateDayOnly }} {{ $t('months.month_' + $options.filters.formatDateMonthOnly(date)) }}</router-link
        >
      </li>
    </ul>
  </section>
</template>

<script>
import { mapState } from "vuex";
import { isOutOfRangeDate, formatDateDayOnly, formatDateMonthOnly } from "@/utils/date";
export default {
  name: "previous-gifts",
  filters: { formatDateDayOnly, formatDateMonthOnly },
  computed: {
    ...mapState({

      dailypromos: state =>
        state.firebase.data.settings && state.firebase.data.settings.dailypromos
    }),
    previousGiftsDates() {
      return (
        this.dailypromos &&
        Object.keys(this.dailypromos).filter(d => isOutOfRangeDate(d))
      );
    }
  }
};
</script>

