<template>
  <div class="main_win">
    <div class="list_components">
        <div v-for="n in 6" v-bind:key="n"  class="content" :id="`id_content${n}`">
            <div class="style_title_content" @click="togglePar(n)">
              {{$t('menu.menu' + n + '~title')}}
            </div>
            <div :id="`id_block${n}`" class="block_with_options">
              <div v-for="k in 6" v-bind:key="k" class="all_options">
                {{$t('menu.menu' + k + '~title')}}
              </div>
            </div>
        </div>
    </div>
    <div class="demonstration">
      lllllllllllll
    </div>
  </div>
</template>



<script>
export default {
  data() {
    return {
      openedPage: false,
      checkId: false,
      saveNumber: 0,
    };
  },
  methods: {
    togglePar: function (a) {
      const id_bl = document.getElementById(`id_block${a}`)  //id_bl     
      if (this.checkId === false) {
        id_bl.style.display = "block"
        this.saveNumber = a      //  
        this.checkId = true      //  
      } else if(this.saveNumber === a){
        document.getElementById(`id_block${this.saveNumber}`).style.display = "none"
        this.saveNumber = 0
        this.checkId = false
      } else if(this.checkId === true){
        document.getElementById(`id_block${this.saveNumber}`).style.display = "none"
        this.saveNumber = 0
        this.checkId = false
        this.togglePar(a)
      }
    }
  }
}
</script>

<style lang="scss">
.main_win{
  display: flex;
  width: 100%;

}
.list_components{
  width: 35%;
}
.s{
  width: 100%;
  color: #fff;
  background-color: #000;

}
.demonstration{
  border: 1px solid green;
  width: 65%;
}
.content{
  margin: 0 10px;
  padding: 10px 20px;
  border-bottom: 1px solid black;
  cursor: pointer;
  .style_title_content{
    color: #8888d7;
  }
  //&.opened {
  //  .block_with_options {
  //    display: block;
  //  }
  //}

  &:hover {
    font-size: 17px;
  }
}

.block_with_options {
  display: none;
}
.all_options {
  &:hover {
    font-size: 16px;
    color: #e8cccc;
    border-right: 1px solid #fff;
  }
}

</style>